.page-template-about {
  .about-text {
    position: absolute;
    color: #fff;
    font-size: 48px;
    font-family: $font__accent;
    text-transform: uppercase;
    top: 175px;
  }

  .about-hero {
    margin-bottom: 20px;
  }

  .about-content-header {
    color: #2f1339;
    font-size: 33.6px;
