/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/

/* Posts and pages */

.site-content {
  padding-top: 100px;
}

.sticky {
  display: block;
}


#secondary{
  display:flex;
}

/* Asides */

.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
  display: none;
}

/* Comments */

.comment-content a {
  word-wrap: break-word;
}
// --------------My Edits------------

.site-content {
  padding-top: 50px;
}
.entry-header {
  position: relative;
  width: 100%;
}

.entry-title a {
  color: white;
}

.entry-header img {
  width: 760.59px;
}

// ----Text for taxonomy links----

.taxonomy-container {
  display: flex;
  width: 100%;
  justify-content: center;
  a {
    padding: 0 2rem;
    font-family: $font__accent;
    text-transform: uppercase;
    font-size: 19px;
    color: #000;
  }
}

// ---------404 Error Page-----

.error404 {
  .page-title {
    text-transform: uppercase;
    font-family: $font__accent;
    font-size: 38px;
    letter-spacing: 2px;
  }
}

.error-404 {
  margin-left: 75px;
  margin-left: 0;
}

.error404 {
  fieldset {
    border: none;
  }
}

.error404 {
  .search-submit {
    display: none;
  }
}

.error404 {
  .widgettitle {
    font-family: $font__accent;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: left;
    color: #2f1339;
  }
}

.error404 {
  .widget_recent_entries {
    ul {
      padding: 0;
      margin: 0;
      font-weight: 300;
    }
  }
}

.widget_categories {
  ul {
    padding: 0;
    margin: 0;
    font-weight: 300;
  }
}

.widget_archive {
  p {
    text-align: left;
    margin: 0;
    width: 500px;
  }
}

// -----Taxonomy Product Type---

.tax-product_type {
  .taxonomy-description {
    p {
      text-align: center;
      font-size: 19.2px;
      padding-bottom: 25px;
    }
  }
}

.taxonomy-container {
  padding-bottom: 50px;
  a:hover {
    color: red;
  }
}

.shop-items-container {
  padding-top: 3rem;
  border-top: 1px dashed #c8c8c8;
}

.adventure-hover:hover {
  color: $border__beige-color;
}

// -------Search--------

.search-results,
.archive.category {
  .page-title {
    text-transform: uppercase;
    font-family: $font__accent;
    font-size: 38px;
    padding-top: 3rem;
    border-bottom: 1px solid #e1e1e1;
    padding-bottom: 1rem;
    width: 800px;
    letter-spacing: 2px;
  }
}

.search-results {
  .page-header {
    border-bottom: 1px solid $image__background-color;
    width: 735px;
  }
}

.search-results {
  .site-content {
    margin-left: 55.5px;
    display: flex;
  }
}

.search-results {
  .search-product-title {
    a {
      color: #248a83;
      text-transform: uppercase;
      font-family: $font__accent;
      font-size: 33px;
      letter-spacing: 2px;
      font-weight: bold;
      display: block;
      padding-top: 4rem;
      &:hover {
        color: #2f1339;
      }
    }
  }
}

.search-product-title {
  p {
    color: black;
    font-size: 16px;
    width: 720px;
    font-family: $font__accent;
    font-weight: normal;
    font-family: $font__main;
    text-transform: none;
    line-height: 1.75;
  }
}

.search-results {
  #inhab-button {
    font-weight: normal;
    padding: 12px 12px;
    width: 145px;
  }
}

.search-results {
  #secondary {
    transform: translatex(78px);
  }
}

.search-results {
  .page-header {
    border-bottom: none;
  }
}

// -----Category Journal------

.archive.tag,
.category,
.date {
  .primary-secondary-container {
    display: flex;
    margin: 0 auto;
  }
}
