// -------Single Products-------

.single {
  .entry-header {
    .entry-meta,
    .nav-previous,
    .nav-next {
      display: none;
    }
  }

  .entry-header {
    .entry-title {
      color: $color__main-orange;
      font-family: $font__accent;
      text-transform: uppercase;
      background-color: transparent;
      font-size: 38.4px;
    }
  }

  article {
    .entry-header {
      img {
        width: 100%;
        object-fit: cover;
        height: 358.38px;
      }
    }
  }

  .social-icons-container {
    margin-bottom: 100px;
  }
}
